import React, { useEffect, useState } from 'react';
import { jsPDF } from 'jspdf';
import NumericKeypad from './NumericKeypad';
import './PaymentModal.css';

export default function PaymentModal({
  saleItems,
  total,
  onClose,
  onSaleProcessed
}) {
  const [paymentMethod, setPaymentMethod] = useState('efectivo');
  const [amountPaid, setAmountPaid] = useState('');

  // Calcula el cambio
  const change = parseFloat(amountPaid || 0) - total;

  // Ajusta el monto según el método de pago
  useEffect(() => {
    if (paymentMethod === 'efectivo') {
      setAmountPaid('');
    } else {
      setAmountPaid(total.toFixed(2));
    }
  }, [paymentMethod, total]);

  const handleKeypadPress = (key) => {
    if (key === 'del') {
      setAmountPaid(prev => prev.slice(0, -1));
    } else {
      setAmountPaid(prev => (prev || '') + key);
    }
  };

  const handleProcess = () => {
    if (paymentMethod === 'efectivo') {
      if (parseFloat(amountPaid) < total) {
        alert("El monto en efectivo es insuficiente.");
        return;
      }
    }
    // Genera PDF de la factura
    try {
      const doc = new jsPDF();
      let y = 10;
      doc.setFontSize(12);
      doc.text("Factura", 10, y); y += 10;
      doc.text(`Fecha: ${new Date().toLocaleString()}`, 10, y); y += 10;
      doc.text(`Pago: ${paymentMethod}`, 10, y); y += 10;

      doc.text("Productos:", 10, y); y += 10;
      saleItems.forEach(item => {
        doc.text(
          `${item.productName} x${item.quantity} = $${(item.price * item.quantity).toFixed(2)}`,
          10, y
        );
        y += 10;
      });
      doc.text(`Total: $${total.toFixed(2)}`, 10, y); y += 10;
      doc.text(`Recibido: $${parseFloat(amountPaid).toFixed(2)}`, 10, y); y += 10;
      const ch = paymentMethod === 'efectivo' ? change.toFixed(2) : '0.00';
      doc.text(`Cambio: $${ch}`, 10, y);

      doc.save("factura.pdf");

      if (onSaleProcessed) {
        onSaleProcessed();
      }
    } catch (err) {
      console.error("Error generando PDF:", err);
      alert("Error procesando venta.");
    }
  };

  return (
    <div className="payment-modal-backdrop">
      <div className="payment-modal">
        <div className="modal-header">
          <h5>Procesar Pago</h5>
          <button className="close btn btn-sm btn-light" onClick={onClose}>
            <i className="bi bi-x-lg"></i>
          </button>
        </div>
        <div className="modal-body">
          <p><strong>Total a Pagar: </strong>${total.toFixed(2)}</p>
          <div className="form-group">
            <label>Tipo de Pago</label>
            <select
              className="form-control"
              value={paymentMethod}
              onChange={e => setPaymentMethod(e.target.value)}
            >
              <option value="efectivo">Efectivo</option>
              <option value="debito">Tarjeta de Débito</option>
              <option value="credito">Tarjeta de Crédito</option>
              <option value="transferencia">Transferencia</option>
              <option value="qr">QR</option>
            </select>
          </div>
          <div className="form-group">
            <label>Monto Recibido</label>
            <input
              type="number"
              className="form-control"
              disabled={paymentMethod !== 'efectivo'}
              value={amountPaid}
              onChange={e => setAmountPaid(e.target.value)}
            />
          </div>
          {paymentMethod === 'efectivo' && (
            <NumericKeypad onKeyPress={handleKeypadPress} />
          )}
          <p>
            <strong>Cambio: </strong>
            {paymentMethod === 'efectivo' && !isNaN(change) ? `$${change.toFixed(2)}` : '$0.00'}
          </p>
        </div>
        <div className="modal-footer">
          <button className="btn btn-success" onClick={handleProcess}>Procesar</button>
          <button className="btn btn-secondary" onClick={onClose}>Cancelar</button>
        </div>
      </div>
    </div>
  );
}
